.share {
  width: 100%;
  /* height: 170px; */
  border-radius: 10px;
  background-color: #fff;
      border: 1px solid lightgray;

  -webkit-box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
  box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
}

.shareWrapper {
  padding: 10px;
  
}

.shareTop {
  display: flex;
  align-items: center;
}

.shareProfileImg {
  width: 55px;
  height: 55px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
    border: 0.1px solid #666464;

}
.shareInput {
  border: none;
  font-size:16px;
  width: 80%;
}

.shareInput:focus {
  outline: none;
}

.shareHr {
  margin: 20px;
  opacity: 50%;
}
.shareBottom {
  display: flex;
  align-items: center;
  justify-content:space-between;
}

.shareOptions{
    display: flex;
    margin-left: 20px;
}

.shareOption{
    display: flex;
    align-items: center;
    margin-right: 15px;
    cursor: pointer;
}

.shareIcon{
    font-size: 18px;
    margin-right: 3px;
}
.shareOptionText{
    font-size: 14px;
    font-weight: 500;
}

.shareButton{
    border: none;
    padding: 10px 12px;
    border-radius: 6px;
    background-color: #1877f2;
    font-weight: 500;
    margin-right: 20px;
    cursor: pointer;
    color: white;
}
.shareButton:disabled{
    border: none;
    padding: 10px 12px;
    border: 1px solid grey;
    border-radius: 6px;
    background-color: #fff;
    font-weight: 500;
    margin-right: 20px;
    color: grey;
    cursor:not-allowed;
}
.shareButton:enabled:hover{
    background-color: #fff;
    color: #1877f2;
    border: 1px solid #1877f2;
}

.shareImgContainer{
  padding: 0 20px 10px 20px;
  position: relative;
}
.shareImg{
  width:100%;
  object-fit: cover;
}
.shareCancelImg{
  position: absolute;
  top: 0;
  right: 20;
  cursor: pointer;
}